.section7
  width: 100vw
  padding-top: 30px

  &__title
    margin-top: 30px
    text-align: center
    font-family: "Exo"
    font-weight: 600
    color: #474646
    font-size: 20px

    &::after
      content: ""
      border-top: 2px solid #fcb900
      width: 60%
      margin-left: 20%
      display: block
      padding-top: 0.2em
      margin-bottom: 30px

  &__content
    width: 80vw
    font-family: "Open Sans"
    font-weight: 300
    font-size: 15px
    margin-left: 10vw

  &__bg
    margin-top: 30px
    height: 120px
    width: 100vw
    background-position: 50% 50%
    background-attachment: scroll
    background-repeat: no-repeat
    background-size: cover
    background-image: url(https://www.intertlc.de/wp-content/uploads/2021/06/modultreppe-2.jpg)

    &__btn
      position: relative
      color: #000
      font-weight: 300
      width: max-content
      background-color: rgba(255,221,0,1)
      box-sizing: border-box
      text-align: center
      padding: 10px 20px
      border-radius: 5px
      border: solid 1px rgba(0,0,0,.05)
      cursor: pointer
      top: 50%
      left: 50%
      transform: translate(-50%, -50%)